export default {
  name: 'CategoryLayout',
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  computed: {
    headers() {
      const prefix = `/user/${this.slug}/emotion/category/`
      return [
        {
          route: `${prefix}bangumi`,
          name: '动漫'
        },
        {
          route: `${prefix}game`,
          name: '游戏'
        },
        {
          route: `${prefix}topic`,
          name: '话题'
        },
        {
          route: `${prefix}notebook`,
          name: '专栏'
        }
      ]
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {}
}
